//mockjs
import Mock from 'mockjs'
//Random
const Random = Mock.Random;
//
Mock.setup({
    timeout:'200-1000'
})
//mock
const produceNewsData = function() {
    let articles = [];
    for (let i = 0; i < 100; i++) {
        let newArticleObject = {
            title: Random.csentence(5, 30), //  Random.csentence( min, max )
            thumbnail_pic_s: Random.dataImage('300x300', 'mock'), // Random.dataImage( size, text )  Base64 
            author_name: Random.cname(), // Random.cname() 
            date: Random.date() + ' ' + Random.time() // Random.date(),yyyy-MM-ddRandom.time() 
        }
        articles.push(newArticleObject)
    }
 
    return {
        articles: articles
    }
}
Mock.mock('/user','get',{
    userid:/^[1-9]{0,1}[0-9]{2}$/,//id
    useravatar: Random.dataImage('300x300', 'mock'), // Random.dataImage( size, text )  Base64 
    username: Random.cname(), // Random.cname() 
    userphone:/^[1]{1}[0-9]{10}$/,//
    address:[],//
    realname:Random.cname(),//
    balance:/^[1-9]{1}[0-9]{1}$/,//
    benefit:/^[1-9]{0,1}[0-9]{1}$/,//
    jifen:/^[1-9]{0,1}[0-9]{0,3}$/,//
    date: Random.date() + ' ' + Random.time() // Random.date(),yyyy-MM-ddRandom.time() 
})
Mock.mock('/user/setname','post',{
    info:'success'
})
Mock.mock('/user/address/','post',{
    info:'success'
})
Mock.mock('/user/forget/vertify','get',function(){// 
    const codenum = Mock.mock({'regexp': /\w{4}/}).regexp;
    return Mock.mock({
        vertifyimg:Random.dataImage('70x30',codenum),
        codenum:codenum
    })
})
Mock.mock('/user/hongbao','get',{
    'hongbaolist|3-5':[
        {
            hong_num:/^[1-9]{1}$/, //
            expire:'2018-05-10',//
            need_num:/^[1-9]{1}[0]{1}$/, //
            hong_type:['','',''] //
        }
    ]
})
Mock.mock('/benefit','post',{
    info:'success'
})
Mock.mock('/benefit/history?userid=10','get',{
    'hongbaolist|8-10':[
        {
            hong_num:/^[1-9]{1}$/, //
            expire:'2018-05-05',//
            need_num:/^[1-9]{1}[0]{1}$/, //
            hong_type:['','',''] //
        }
    ] 
})
var orderlist = ()=>{
    let arr =[]
    for(let i=0;i<10;++i){
        arr.push(
            Mock.mock({
                'pay_num|1-100':1,
                'store_img|1':Random.dataImage('50x50','abcd'),
                'store_name|1':Random.cname()+''
            })
        )
    }
    return arr
}
Mock.mock('/order?userid=10','get',orderlist)
Mock.mock('/excard','post',{
    info:'success'
})
Mock.mock('/card/cardcord?userid=10&currentPage=1','get',{
    'paylist|10':[
        {
            pay_num:/^[1-9]{1}[0]{1}$/, //
            date:'2018-05-10',//
            viptype:''//
        }
    ]
})


