<template>
 <div class="wrap">
     <div class="usecard_wrap">
         <p class="info"><b>{{username}}</b></p>
         <div class="card_wrap">
             <input type="text" v-model="cardname" placeholder="10">
             <input type="password" v-model="cardpass" placeholder="6">
         </div>
         <span :class="{active:isChecked}" class="excard" @click="excard"></span>
         <div class="tips">
             <h3></h3>
             <p></p>
             <p>30</p>
             <p>90</p>
             <p>360</p>
             <p>3431</p>
         </div>
     </div>
     <headTop head-title=""></headTop>
     <alert ref="alert"></alert>
     <loading ref="loading"></loading>
 </div>
</template>

<script>
import headTop from '../../../components/head'
import alert from '../../../components/alert'
import loading from '../../../components/loading'
import axios from 'axios'
export default {
 name:'usecard',
 data(){
     return {
        cardname:'',
        cardpass:'',
     }
 },
 mounted () {
 },
 components:{
     headTop,
     alert,
     loading
 },
 methods:{
     excard(){
         if(this.isChecked){
             this.$refs.loading.show();
             axios.post('/excard',{
                 params:{
                     userid:this.$store.state.userinfo.userid
                 }
             }).then(res=>{
                 this.$refs.loading.hide()
                 if(res.data.info =='success'){
                     this.$refs.alert.show('',true)
                 }else if(res.data.info == 'wrong'){
                     this.$refs.alert.show('')
                     this.cardname = this.cardpass ='';
                 }else {
                     this.$refs.alert.show('');
                     this.cardname =this.cardpass = '';
                 }
             })
         }else {
             this.$refs.alert.show('')
         }
     }
 },
 computed:{
     username(){
         return this.$store.state.userinfo.username;
     },
     isChecked(){
         if(this.cardname.trim().length == 10 && this.cardpass.trim().length == 6){
             return true;
         }else {
             return false;
         }
     }
 }
}
</script>
<style lang="scss" scoped>
 .usecard_wrap {
     padding-top: 40px;
     background: #f5f5f5;
     height: 100%;
     overflow: auto;
     text-align: left;
     font-size: 14px;
 }
 .info {
     padding: 10px 15px;

 }
 .card_wrap {
     input {
         width: 100%;
         padding: 15px 0 15px 15px;
         border:  none;
         color: #666;
         &:nth-child(1) {
             border-bottom: 1px solid #e8e8e8;
         }
         &:focus {
             outline: none;
         }
     }
 }
 .excard {
     width: 90%;
     height: 40px;
     line-height: 40px;
     font-size: 16px;
     display: block;
     margin: 20px auto;
     text-align: center;
     background: #ccc;
     color: white;
     border-radius: 5px;
     opacity: .9;
     &:active {
         opacity: 1;
     }
 }
 .active {
     background: #56d176;
 }
 .tips {
     padding: 20px 15px 10px 20px;
     color: #aaa;
     font-size: 12px;
     line-height: 18px;
     h3 {
         text-align: center;
         font-size: 16px;
         padding: 10px 0;
     }
 }
</style>


